import { Injectable } from '@angular/core';
import { IDefinitionAdapter } from '@helix/platform/shared/api';
import { IContainerViewComponentDefinition } from '../domain/view-component-definition.types';
import { IViewDefinition } from '../domain/view-definition.interface';

@Injectable()
export class RxViewActionDesignAdapterService
  implements IDefinitionAdapter<IContainerViewComponentDefinition, IViewDefinition>
{
  adaptDefinition(componentDefinition: IContainerViewComponentDefinition) {
    componentDefinition.propertiesByName.$condition$ = componentDefinition.propertiesByName.$condition$ ?? null;
  }
}
